import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import FlightDetail from './flights/FlightDetail';
import { useState, useEffect } from 'react';
import FlightModal from './flights/FlightModal';
import Pagination from 'react-bootstrap/Pagination';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Collapse from 'react-bootstrap/Collapse';
import { useAuth } from '../contexts/AuthContext';
// //generated from https://www.mockaroo.com/14dc7090
// const names = require("../data/names3.json");


//import flightsFromFile from "../data/flights.json";


const baseApiUrl = import.meta.env.VITE_API_BASE_URL;
//const  dailiesBaseUrl = "https://localhost:5001/v2/api/Dailies";
const dailiesBaseUrl = `${baseApiUrl}/v2/api/Dailies`;
console.log("dailiesBaseUrl: " + dailiesBaseUrl);

// Default values
const DEFAULT_ROLL_ON = 600;
const DEFAULT_ROLL_OFF = -30;
const DEFAULT_ADI = '';

function addMinutes(date, minutes) {
    return new Date(date.getTime() + minutes*60000);
}

function Flights() {
    console.log("Using Flights API");
    const { authenticatedFetch } = useAuth();
    const [show, setShow] = useState(false);
    const [modalFlight, setModalFlight] = useState({});
    const [currentPage, setCurrentPage] = useState(1);
    const [flights, setFlights] = useState([]);
    const [totalRecords, setTotalRecords] = useState(0);
    const [loading, setLoading] = useState(false);
    const [itemsPerPage, setItemsPerPage] = useState(50);
    
    // Configurable filters with local storage persistence
    const [rollOn, setRollOn] = useState(() => {
        const saved = localStorage.getItem('flights-rollOn');
        return saved !== null ? (saved === '' ? '' : parseInt(saved)) : DEFAULT_ROLL_ON;
    });
    const [rollOff, setRollOff] = useState(() => {
        const saved = localStorage.getItem('flights-rollOff');
        return saved !== null ? (saved === '' ? '' : parseInt(saved)) : DEFAULT_ROLL_OFF;
    });
    const [adi, setAdi] = useState(() => {
        const saved = localStorage.getItem('flights-adi');
        return saved || DEFAULT_ADI;
    });
    const [showFilters, setShowFilters] = useState(false);

    // Applied versions for API calls
    const [appliedRollOn, setAppliedRollOn] = useState(rollOn);
    const [appliedRollOff, setAppliedRollOff] = useState(rollOff);
    const [appliedAdi, setAppliedAdi] = useState(adi);

    // Save to local storage whenever values change
    useEffect(() => {
        localStorage.setItem('flights-rollOn', rollOn.toString());
    }, [rollOn]);
    
    useEffect(() => {
        localStorage.setItem('flights-rollOff', rollOff.toString());
    }, [rollOff]);
    
    useEffect(() => {
        localStorage.setItem('flights-adi', adi);
    }, [adi]);

    // Function to apply filters
    const applyFilters = () => {
        setAppliedRollOn(rollOn);
        setAppliedRollOff(rollOff);
        setAppliedAdi(adi);
    };

    // Check if filters have changed from applied values
    const filtersChanged = rollOn !== appliedRollOn || rollOff !== appliedRollOff || adi !== appliedAdi;

    const numPages = Math.ceil(totalRecords / itemsPerPage);

    const getRecords = (page = 1) => {
        console.log("Fetching data for page:", page);
        setLoading(true);
        const skip = (page - 1) * itemsPerPage;
        
        // Use defaults for empty values
        const effectiveRollOn = appliedRollOn === '' ? DEFAULT_ROLL_ON : appliedRollOn;
        const effectiveRollOff = appliedRollOff === '' ? DEFAULT_ROLL_OFF : appliedRollOff;
        
        // Build URL with configurable parameters
        let url = `${dailiesBaseUrl}/GetFlightsByTimeRange?rollOn=${effectiveRollOn}&rollOff=${effectiveRollOff}&skip=${skip}&top=${itemsPerPage}`;
        
        // Add ADI filter if specified
        if (appliedAdi.trim()) {
            url += `&adi=${encodeURIComponent(appliedAdi.trim())}`;
        }
        
        console.log("Fetching URL: " + url);
        
        authenticatedFetch(url, {
          method: 'GET'
        })
        .then(response => {
            if (!response.ok) {
              throw new Error(`HTTP error! Status: ${response.status}`);
            }
            
            // Get total count from header
            const totalCount = response.headers.get('x-total-count');
            if (totalCount) {
                setTotalRecords(parseInt(totalCount));
            }
            
            return response.json();
          })
          .then(data => {
            console.log('Data:', data);
            console.log('Total records from header:', totalRecords);
            
            // Handle different response formats
            if (Array.isArray(data)) {
              setFlights(data);
            } else if (data && data.results) {
              // If API returns an object with results and count
              setFlights(data.results);
              // Only use data count if header wasn't available
              if (!totalRecords) {
                setTotalRecords(data.totalCount || data.count || 0);
              }
            } else {
              setFlights(data);
              // Only use data length if header wasn't available
              if (!totalRecords) {
                setTotalRecords(data?.length || 0);
              }
            }
            
            setLoading(false);
          })
          .catch(error => {
            console.error('Error fetching data:', error);
            setLoading(false);
          });
    }

    useEffect(() => {
        setCurrentPage(1); // Reset to first page when filters change
        getRecords(1);
    }, [appliedRollOn, appliedRollOff, appliedAdi, itemsPerPage]);

    useEffect(() => {
        getRecords(currentPage);
    }, [currentPage]);

    const handleClose = () => setShow(false);


    function ShowFlight(flight){
      setModalFlight(flight);
      setShow(true);
    }


    function setPage(page){
        if (page >= 1 && page <= numPages && page !== currentPage){
            console.log("Setting page to " + page);
            setCurrentPage(page);
        } else {
            console.log("Not setting page because page is out of range or same page: " + page);
        }
    }

    function handlePageSizeChange(event) {
        const newPageSize = parseInt(event.target.value);
        console.log("Changing page size to:", newPageSize);
        setItemsPerPage(newPageSize);
        setCurrentPage(1); // Reset to first page when changing page size
    }

    function paginationClicked(event) {
        const itemClicked = parseInt(event.target.textContent);
        console.log("itemClicked: " + itemClicked);
        if (!isNaN(itemClicked)) {
            setPage(itemClicked);
        }
    }

    console.log("Current page: " + currentPage);
    console.log("Total records: " + totalRecords);
    console.log("Number of pages: " + numPages);

    let paginationItems = [];
    
    // Show pagination only if there are multiple pages
    if (numPages > 1) {
        // Show first few pages, current page context, and last few pages
        const showPages = [];
        
        if (numPages <= 7) {
            // Show all pages if 7 or fewer
            for (let i = 1; i <= numPages; i++) {
                showPages.push(i);
            }
        } else {
            // Show first page, current page context, and last page
            showPages.push(1);
            
            if (currentPage > 3) {
                showPages.push('...');
            }
            
            for (let i = Math.max(2, currentPage - 1); i <= Math.min(numPages - 1, currentPage + 1); i++) {
                if (!showPages.includes(i)) {
                    showPages.push(i);
                }
            }
            
            if (currentPage < numPages - 2) {
                showPages.push('...');
            }
            
            if (!showPages.includes(numPages)) {
                showPages.push(numPages);
            }
        }
        
        showPages.forEach((page, index) => {
            if (page === '...') {
                paginationItems.push(
                    <Pagination.Ellipsis key={`ellipsis-${index}`} />
                );
            } else {
                paginationItems.push(
                    <Pagination.Item 
                        key={page} 
                        active={page === currentPage} 
                        onClick={(event) => paginationClicked(event)}
                    >
                        {page}
                    </Pagination.Item>
                );
            }
        });
    }



    return (
        <div className="main-content p-3" style={{ minHeight: '100vh', background: 'linear-gradient(135deg, #e0e7ef 60%, #f1f5f9 100%)' }}>
            <Container fluid>
                <Card className="shadow-sm mb-4 border-0 rounded-4">
                    <Card.Header className="bg-primary text-white d-flex align-items-center rounded-top-4" style={{ fontSize: '1.5rem' }}>
                        <i className="bi bi-airplane me-2"></i> Flight Management
                    </Card.Header>
                    <Card.Body>
                        {loading && (
                            <div className="text-center my-4">
                                <div className="spinner-border text-primary" role="status">
                                    <span className="visually-hidden">Loading...</span>
                                </div>
                                <p className="mt-2">Loading flights...</p>
                            </div>
                        )}
                        {/* Filter Controls */}
                        <div className="mb-4">
                            <Button
                                variant="outline-primary"
                                onClick={() => setShowFilters(!showFilters)}
                                aria-controls="filter-controls"
                                aria-expanded={showFilters}
                                size="sm"
                            >
                                {showFilters ? 'Hide' : 'Show'} Filters
                            </Button>
                            <Collapse in={showFilters}>
                                <div id="filter-controls">
                                    <Card className="mt-3 border-0 shadow-sm rounded-3">
                                        <Card.Body>
                                            <h6 className="mb-3"><i className="bi bi-funnel me-2"></i>Flight Filters</h6>
                                            <Row className="g-3">
                                                <Col md={2}>
                                                    <Form.Group>
                                                        <Form.Label>Roll On (minutes)</Form.Label>
                                                        <Form.Control
                                                            type="number"
                                                            value={rollOn}
                                                            onChange={(e) => {
                                                                const value = e.target.value;
                                                                setRollOn(value === '' ? '' : parseInt(value) || 0);
                                                            }}
                                                            size="sm"
                                                        />
                                                        <Form.Text className="text-muted">
                                                            Minutes before scheduled time
                                                        </Form.Text>
                                                    </Form.Group>
                                                </Col>
                                                <Col md={2}>
                                                    <Form.Group>
                                                        <Form.Label>Roll Off (minutes)</Form.Label>
                                                        <Form.Control
                                                            type="number"
                                                            value={rollOff}
                                                            onChange={(e) => {
                                                                const value = e.target.value;
                                                                setRollOff(value === '' ? '' : parseInt(value) || 0);
                                                            }}
                                                            size="sm"
                                                        />
                                                        <Form.Text className="text-muted">
                                                            Minutes after scheduled time
                                                        </Form.Text>
                                                    </Form.Group>
                                                </Col>
                                                <Col md={2}>
                                                    <Form.Group>
                                                        <Form.Label>ADI Filter</Form.Label>
                                                        <Form.Select
                                                            value={adi}
                                                            onChange={(e) => setAdi(e.target.value)}
                                                            size="sm"
                                                        >
                                                            <option value="">No Filter</option>
                                                            <option value="A">A - Arrival</option>
                                                            <option value="D">D - Departure</option>
                                                        </Form.Select>
                                                        <Form.Text className="text-muted">
                                                            Filter by flight type
                                                        </Form.Text>
                                                    </Form.Group>
                                                </Col>
                                                <Col md={6}>
                                                    <Form.Group>
                                                        <Form.Label>&nbsp;</Form.Label>
                                                        <div className="d-flex gap-2">
                                                            <Button
                                                                variant="primary"
                                                                size="sm"
                                                                onClick={applyFilters}
                                                                disabled={!filtersChanged}
                                                            >
                                                                Apply Filters
                                                            </Button>
                                                            <Button
                                                                variant="outline-secondary"
                                                                size="sm"
                                                                onClick={() => {
                                                                    setRollOn(DEFAULT_ROLL_ON);
                                                                    setRollOff(DEFAULT_ROLL_OFF);
                                                                    setAdi(DEFAULT_ADI);
                                                                }}
                                                            >
                                                                Reset Filters
                                                            </Button>
                                                        </div>
                                                    </Form.Group>
                                                </Col>
                                            </Row>
                                        </Card.Body>
                                    </Card>
                                </div>
                            </Collapse>
                        </div>
                        <div className="d-flex justify-content-between align-items-center mb-4">
            <div className="row w-100 g-2 align-items-center">
                <div className="col-12 col-sm-auto d-flex align-items-center mb-2 mb-sm-0">
                    <span className="me-2">Show:</span>
                    <Form.Select 
                        value={itemsPerPage} 
                        onChange={handlePageSizeChange}
                        style={{ width: 'auto' }}
                        size="sm"
                    >
                        <option value={10}>10</option>
                        <option value={25}>25</option>
                        <option value={50}>50</option>
                        <option value={100}>100</option>
                    </Form.Select>
                    <span className="ms-2">flights per page</span>
                </div>
                {numPages > 1 && (
                    <div className="col-12 col-sm d-flex justify-content-sm-end w-100 overflow-auto">
                        <Pagination size="sm" className="flex-nowrap">
                            <Pagination.First 
                                onClick={() => setPage(1)} 
                                disabled={currentPage === 1 || loading}
                            />
                            <Pagination.Prev 
                                onClick={() => setPage(currentPage - 1)} 
                                disabled={currentPage === 1 || loading}
                            />
                            {paginationItems}
                            <Pagination.Next 
                                onClick={() => setPage(currentPage + 1)} 
                                disabled={currentPage === numPages || loading}
                            />
                            <Pagination.Last 
                                onClick={() => setPage(numPages)} 
                                disabled={currentPage === numPages || loading}
                            />
                        </Pagination>
                    </div>
                )}
            </div>
                        </div>
                        <div className="mb-3 text-muted">
                            Showing {flights.length} of {totalRecords} flights
                            {numPages > 1 && ` (Page ${currentPage} of ${numPages})`}
                            {` • ${itemsPerPage} per page`}
                        </div>
                        <Row xs={1} sm={2} md={3} lg={4} xl={5}>
                            {flights.map((flight) =>
                                <Col className="mb-3" key={flight.id}>
                                    <FlightDetail flight={flight} showFlight={ShowFlight} />
                                </Col>
                            )}
                        </Row>
                        {flights.length === 0 && !loading && (
                            <div className="text-center py-5">
                                <h4>No flights found</h4>
                                <p className="text-muted">Try adjusting your search criteria or check back later.</p>
                            </div>
                        )}
                        {numPages > 1 && (
                            <div className="d-flex justify-content-center mt-4 w-100 overflow-auto">
                                <Pagination className="flex-nowrap">
                                    <Pagination.First 
                                        onClick={() => setPage(1)} 
                                        disabled={currentPage === 1 || loading}
                                    />
                                    <Pagination.Prev 
                                        onClick={() => setPage(currentPage - 1)} 
                                        disabled={currentPage === 1 || loading}
                                    />
                                    {paginationItems}
                                    <Pagination.Next 
                                        onClick={() => setPage(currentPage + 1)} 
                                        disabled={currentPage === numPages || loading}
                                    />
                                    <Pagination.Last 
                                        onClick={() => setPage(numPages)} 
                                        disabled={currentPage === numPages || loading}
                                    />
                                </Pagination>
                            </div>
                        )}
                    </Card.Body>
                </Card>
            </Container>
            <FlightModal show={show} flight={modalFlight} handleClose={handleClose} />
        </div>
    );
}

export default Flights;