import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Stack } from 'react-bootstrap';
import FlightMethods from '../../utility/flights/FlightMethods';
import Form from 'react-bootstrap/Form';
import { useEffect, useState } from 'react';
import { useFields } from '../../contexts/FieldsContext';
import FilterableCitySelect from './FilterableCitySelect';
import FilterableGateSelect from './FilterableGateSelect';
import FilterableStatusSelect from './FilterableStatusSelect';
import Spinner from 'react-bootstrap/Spinner';
import { toast } from 'react-toastify';
import { useAuth } from '../../contexts/AuthContext';
import { DateTime } from "luxon";

//This version uses form submit and grabs the variables.  It's a bit simpler, since it doesn't use effects or state

function FlightModal({ flight, show, handleClose }) {
    const { authenticatedFetch, claims } = useAuth();
    const baseApiUrl = import.meta.env.VITE_API_BASE_URL;
    const dailiesBaseUrl = `${baseApiUrl}/v2/api/Dailies`;
    const { byField, fields: fieldsContext, loading: fieldsLoading } = useFields();
    // Only call getFormattedFlight after fieldsContext is loaded
    const formattedFlight = (!fieldsLoading && fieldsContext && Object.keys(fieldsContext).length > 0)
        ? FlightMethods.getFormattedFlight(flight, fieldsContext)
        : {};
    const [city, setCity] = useState("");
    const [status, setStatus] = useState("");
    let cityOptions = byField?.city || [];
    let gateOptions = byField?.gate || [];

    // State for actual time picker (store in API format)
    const [actualTime, setActualTime] = useState(flight.actual || "");
    // Keep actualTime in sync with flight.actual when modal opens or flight.actual changes
    useEffect(() => {
        setActualTime(flight.actual || "");
    }, [show, flight.actual]);
    // If the current city code is not in the options, add it as a custom option
    if (city && !cityOptions.some(opt => opt.code === city)) {
        cityOptions = [
            { code: city, value: city },
            ...cityOptions
        ];
    }
    // If the current gate is not in the options, add it as a custom option
    const [gate, setGate] = useState("");
    if (gate && !gateOptions.some(opt => opt.code === gate)) {
        gateOptions = [
            { code: gate, value: gate },
            ...gateOptions
        ];
    }
    const statusOptions = byField?.status || [];

    // Set city value when modal opens or when fields are loaded
    useEffect(() => {
        if (show && formattedFlight.iata) {
            setCity(formattedFlight.iata);
        }
        if (show && formattedFlight.statusCode) {
            setStatus(formattedFlight.statusCode);
        }
        if (show && formattedFlight.gate) {
            setGate(formattedFlight.gate);
        }
    }, [show, formattedFlight.iata, formattedFlight.statusCode, formattedFlight.gate]);

    // Helper to get field value from flight.fields
    function getFieldValue(fieldName) {
        const arr = flight.fields?.[fieldName];
        if (Array.isArray(arr) && arr.length > 0) {
            return arr.find(f => f.instance === 1)?.value;
        }
        return undefined;
    }

    // Build JSON Patch array for changed fields
    function buildPatch() {
        const patch = [];
        // ACTUAL (property, not in fields)
        if (actualTime && actualTime !== flight.actual) {
            if (claims && claims.TimeZone) {
                // Use Luxon to get offset and format
                const tz = claims.TimeZone;
                console.log("Using TimeZone from claims:", tz);
                // actualTime is 'YYYY-MM-DDTHH:mm' from input
                let dt = DateTime.fromFormat(actualTime, "yyyy-MM-dd'T'HH:mm", { zone: tz });
                // If parsing fails, fallback to ISO
                if (!dt.isValid) {
                    dt = DateTime.fromISO(actualTime, { zone: tz });
                }
                // Format as 'YYYY-MM-DDTHH:mm:ss±HH:mm'
                const actualWithOffset = dt.toFormat("yyyy-MM-dd'T'HH:mm:ssZZ");
                patch.push({ op: flight.actual ? "replace" : "add", path: "/actual", value: actualWithOffset });
            } else {
                console.log("No TimeZone found in claims, using time zone from flight data");
                // Fallback: preserve original logic
                let actualWithOffset = actualTime;
                if (flight.actual) {
                    const match = flight.actual.match(/([+-][0-9]{2}:[0-9]{2})$/);
                    const offset = match ? match[1] : undefined;
                    let dt = DateTime.fromFormat(actualTime, "yyyy-MM-dd'T'HH:mm");
                    if (!dt.isValid) {
                        dt = DateTime.fromISO(actualTime);
                    }
                    if (offset) {
                        actualWithOffset = dt.toFormat("yyyy-MM-dd'T'HH:mm") + offset;
                    } else {
                        actualWithOffset = actualTime;
                    }
                }
                patch.push({ op: flight.actual ? "replace" : "add", path: "/actual", value: actualWithOffset });
            }
        }
        // GATE, CITY, STATUS (fields dictionary)
        ["gate", "city", "status"].forEach(field => {
            const newValue = ({ gate, city, status })[field];
            // Get the first item in the array, if any
            const arr = flight.fields?.[field];
            const oldValue = Array.isArray(arr) && arr.length > 0 ? arr[0].value : undefined;
            if (newValue && newValue !== oldValue) {
                if (Array.isArray(arr) && arr.length > 0) {
                    // Update the first item, regardless of instance
                    patch.push({ op: "replace", path: `/fields/${field}/0/value`, value: newValue });
                } else {
                    // Field doesn't exist or is empty, add a new item
                    patch.push({ op: "add", path: `/fields/${field}`, value: [{ instance: 1, value: newValue }] });
                }
            }
        });
        return patch;
    }

    // PATCH request to /v2/api/Dailies/{id}
    const updateFlight = (id, patch) => {
        const url = `${dailiesBaseUrl}/${id}`;
        return authenticatedFetch(url, {
            method: 'PATCH',
            headers: {
                'Content-Type': 'application/json-patch+json'
            },
            body: JSON.stringify(patch)
        })
        .then(response => {
            if (!response.ok) {
                throw new Error(`HTTP error! Status: ${response.status}`);
            }
            return response.json();
        });
    }


    // Format for display
function formatDateTime(value, timeFormat) {
    if (!timeFormat) timeFormat = "MM/dd/yyyy hh:mm a";
    if (!value) return "";
    let dt = DateTime.fromISO(value);
    if (!dt.isValid) {
        dt = DateTime.fromFormat(value, "yyyy-MM-dd HH:mm");
    }
    return dt.isValid ? dt.toFormat(timeFormat) : value;
}


    function getLinecodeNumber() {
        const result = FlightMethods.getCodeshareField(flight, "linecode") + " " + FlightMethods.getCodeshareField(flight, "number");
        return result;
    }

    function setField(name, value, updatedFields) {

        var dailyValue = {field: name, instance: 1, value: value};
        updatedFields.push(dailyValue);

        flight.fields[name] = [{ instance: 1, value: value }];
    }


    // Only update actual time in state, not API
    // Convert from datetime-local input to API format on change
    const handleActualTimeChange = (e) => {
        const newValue = e.target.value;
        // newValue is in 'YYYY-MM-DDTHH:mm', convert to 'YYYY-MM-DD HH:mm'
        //setActualTime(newValue ? moment(newValue).format('YYYY-MM-DD HH:mm') : "");
        if (newValue)
            setActualTime(newValue);
    };


    const handleSubmit = async (event) => {
        event.preventDefault();
        const patch = buildPatch();
        console.log("PATCH ID:", flight.id, "payload:", patch);
        if (patch.length === 0) {
            toast.info("No changes to save.");
            return;
        }
        try {
            await updateFlight(flight.id, patch);
            // Update local flight object for changed fields
            patch.forEach(op => {
                if (op.path === "/actual") {
                    flight.actual = op.value;
                } else if (op.path.startsWith("/fields/")) {
                    const [, , field, idxOrDash, prop] = op.path.split("/");
                    if (field && prop === "value") {
                        if (!Array.isArray(flight.fields[field])) flight.fields[field] = [];
                        const idx = idxOrDash === "-" ? flight.fields[field].length : parseInt(idxOrDash, 10);
                        flight.fields[field][idx] = { instance: 1, value: op.value };
                    } else if (field && !prop) {
                        // Add whole field array, ensure it's always an array
                        flight.fields[field] = Array.isArray(op.value) ? op.value : [op.value];
                    }
                }
            });
            handleClose();
            toast.success("Record was saved successfully");
        } catch (error) {
            toast.error("Record did not save successfully.  Error reported: " + error);
        }
    };



    if (fieldsLoading || !fieldsContext || Object.keys(fieldsContext).length === 0) {
        return (
            <Modal show={show} onHide={handleClose} centered>
                <Modal.Body className="text-center py-5">
                    <Spinner animation="border" role="status" />
                    <div className="mt-3">Loading fields...</div>
                </Modal.Body>
            </Modal>
        );
    }

    return (
        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
                <Modal.Title>{getLinecodeNumber()}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form onSubmit={handleSubmit}>
                    <Form.Group className="mb-3" controlId="id">
                        <Form.Label>ID</Form.Label>
                        <Form.Control type="text" disabled={true} value={flight.id} />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="linecode_number">
                        <Form.Label>Flight</Form.Label>
                        <Form.Control type="text" disabled={true} defaultValue={formattedFlight.linecodeNumber} />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="schedule_time">
                        <Form.Label>Schedule</Form.Label>
                        <Form.Control type="text" disabled={true} value={formatDateTime(flight.schedule)} />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="actual_time">
                        <Form.Label>Actual</Form.Label>
                        <Form.Control
                            type="datetime-local"
                        value={actualTime ? DateTime.fromISO(actualTime).toFormat('yyyy-MM-dd\'T\'HH:mm') : ""}
                            onChange={handleActualTimeChange}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="gate">
                        <Form.Label>Gate</Form.Label>
                        <FilterableGateSelect
                            options={gateOptions}
                            value={gate}
                            onChange={setGate}
                            name="gate"
                            inputId="gate-select"
                            isCreatable={true}
                            formatCreateLabel={inputValue => `Add "${inputValue}"`}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="city">
                        <Form.Label>City</Form.Label>
                        <FilterableCitySelect
                            options={cityOptions}
                            value={city}
                            onChange={setCity}
                            name="city"
                            inputId="city-select"
                        />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="status">
                        <Form.Label>Status</Form.Label>
                        <FilterableStatusSelect
                            options={statusOptions}
                            value={status}
                            onChange={setStatus}
                            name="status"
                            inputId="status-select"
                        />
                    </Form.Group>
                    <Button type="submit" variant="primary">
                        Save Changes
                    </Button>  
                    <span className="p-2" />                  
                    <Button variant="secondary" onClick={handleClose}>
                        Cancel
                    </Button>
                </Form>
            </Modal.Body>
            <Modal.Footer>
            </Modal.Footer>
        </Modal>
    );
}

export default FlightModal;