import { DateTime } from "luxon";

function FlightMethods(){
      const imageBaseUrl = "https://abc.dev.cloudids.aero/images/";

     function formatTime(value, timeFormat){
        if (!timeFormat) timeFormat = "h:mm a";
        if (!value) return "";
        let dt = DateTime.fromISO(value);
        if (!dt.isValid) {
            dt = DateTime.fromFormat(value, "yyyy-MM-dd HH:mm");
        }
        return dt.isValid ? dt.toFormat(timeFormat) : value;
      }
    
      function getLookup(flight, fields, field, valueType, language){
            if (!valueType) valueType = "default";
            if (!language) language = "en";
            var dailyValue = getDailyValue(flight, field);
            var key = `${field}/${dailyValue}/${language}/${valueType}`.toLowerCase();
            var result = fields[key]?.value ?? dailyValue;
            return result;
    
      }
    
      // function getDailyValue(flight, field) {
      //   //var result = flight?.fields?.[field]?.[0]?.value;
      //   var result = flight.dailyValues?.find(dv => dv.field == field && dv.instance == 1)?.value;
      //   return result ? result : "";
      // }

      function getDailyValue(flight, field, instance = 1) {
        var result = flight?.fields?.[field]?.[0]?.value;
        //var result = flight?.fields?.[field]?.find(f => f.instance === instance)?.value;
        return result;
      }
    
      // function getCodeshareField(flight, field) {
      //   // var result = flight?.fieldGroups?.codeshares?.[0]?.[field]?.value;
      //   // if (!result)
      //   var result = getDailyValue(flight, field);
      //   return result;
      // }

      function getCodeshareField(flight, field) {
        var result = flight?.fieldGroups?.codeshares?.[0]?.[field]?.value;
        if (!result)
          result = getDailyValue(flight, field);
        return result;
      }
    
      function getFormattedFlight(flight, fields){
          let logoFile = getCodeshareField(flight, "logo");
          // Use the remote base URL for airline logos, but use local BASE_URL/images/default.png for missing logos
          let baseUrl = import.meta.env.BASE_URL || "/";
          let logoUrl = logoFile ? (imageBaseUrl + logoFile) : `${baseUrl}images/default.png`;
          var result = {
              id: flight.id, 
              adi: flight?.adi,
              schedule: formatTime(flight?.schedule),
              actual: formatTime(flight?.actual),
              iata: getDailyValue(flight, "city"),
              city: getLookup(flight, fields, "city"),
              logo: logoUrl,
              linecode: getCodeshareField(flight, "linecode"),
              number: getCodeshareField(flight, "number"),
              gate: getDailyValue(flight, "gate"),
              gateLookup: getLookup(flight, fields, "gate"),
              status: getLookup(flight, fields, "status"),
              statusCode: getDailyValue(flight, "status"),
              linecodeNumber: getCodeshareField(flight, "linecode") + " " + getCodeshareField(flight, "number")
          };
          return result;
      }

      return {getFormattedFlight, formatTime, getLookup, getDailyValue, getCodeshareField};
}

export default new FlightMethods();

