import Card from 'react-bootstrap/Card';
import { Stack } from 'react-bootstrap';
import { useState } from 'react';
import { useFields } from '../../contexts/FieldsContext';
import '../SomeCard.scss';
import FlightMethods from '../../utility/flights/FlightMethods';

function FlightDetail({flight, showFlight}) {
  const [imageError, setImageError] = useState(false);
  const { fields } = useFields();
  var formattedFlight = FlightMethods.getFormattedFlight(flight, fields);

  // Placeholder logo component
  const PlaceholderLogo = () => (
    <div className="flight-logo-placeholder">
      <div className="placeholder-content">
        <div className="airline-code">{formattedFlight.iata || 'N/A'}</div>
        <div className="flight-number">{formattedFlight.linecode}{formattedFlight.number}</div>
      </div>
    </div>
  );


  return (
    <Card className="myCard" onClick={() => showFlight(flight)}>
      <Card.Body>
        <div className="flight-content">
          <Card.Title className="flight-title">
            {flight.id} ({flight.adi}) {formattedFlight.linecode}{formattedFlight.number}
          </Card.Title>
          <div className="flight-info">
            <div className="text-center">
              {!imageError ? (
                <img 
                  src={formattedFlight.logo} 
                  alt={`${formattedFlight.iata} logo`}
                  className="flight-logo"
                  onError={() => setImageError(true)}
                />
              ) : (
                <PlaceholderLogo />
              )}
            </div>
            <div><strong>IATA:</strong> {formattedFlight.iata}</div>
            <div><strong>City:</strong> {formattedFlight.city}</div>
            <div><strong>Gate:</strong> {formattedFlight.gate}</div>
            <div><strong>Status:</strong> {formattedFlight.status}</div>
            <div><strong>Schedule:</strong> {formattedFlight.schedule}</div>
          </div>
        </div>
      </Card.Body>
    </Card>
  );
}

export default FlightDetail;